
  <template>
  <div>
    <el-divider content-position="left">Transfer</el-divider>
    <el-transfer
      filterable
      :filter-method="filterMethod"
      filter-placeholder=""
      v-model="value"
      :data="data">
    </el-transfer>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Divider as __Divider__,Transfer as __Transfer__} from 'element-ui';
     
     
     
     
     export default {
    data() {
      const generateData = _ => {
        const data = [];
        const cities = ['', '', '', '', '', '', ''];
        const pinyin = ['shanghai', 'beijing', 'guangzhou', 'shenzhen', 'nanjing', 'xian', 'chengdu'];
        cities.forEach((city, index) => {
          data.push({
            label: city,
            key: index,
            pinyin: pinyin[index]
          });
        });
        return data;
      };
      return {
        data: generateData(),
        value: [],
        filterMethod(query, item) {
          return item.pinyin.indexOf(query) > -1;
        }
      };
    }
  };
     __vue__.use(__Divider__);
__vue__.use(__Transfer__)
     
    
  </script>

  