
  <template>
  <div>
    <el-divider content-position="left">Message</el-divider>
    <el-row>
      <el-button :plain="true" @click="open1"></el-button>
      <el-button :plain="true" @click="open2"></el-button>
      <el-button :plain="true" @click="open3"></el-button>
      <el-button :plain="true" @click="open4"></el-button>
      <el-button type="text" @click="open"> Message Box</el-button>
      <el-button
        plain
        @click="open5">
        
      </el-button>
      <el-button
        plain
        @click="open6">
        
      </el-button>
      <el-button
        plain
        @click="open7">
        
      </el-button>
      <el-button
        plain
        @click="open8">
        
      </el-button>
    </el-row>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Divider as __Divider__,Row as __Row__} from 'element-ui';
     
     
     
     
     export default {
    methods: {
      open1() {
        this.$message({
          showClose: true,
          message: ''
        });
      },

      open2() {
        this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
      },

      open3() {
        this.$message({
          showClose: true,
          message: '',
          type: 'warning'
        });
      },

      open4() {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      },
      open() {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      open5() {
        this.$notify({
          title: '',
          message: '',
          type: 'success'
        });
      },

      open6() {
        this.$notify({
          title: '',
          message: '',
          type: 'warning'
        });
      },

      open7() {
        this.$notify.info({
          title: '',
          message: ''
        });
      },

      open8() {
        this.$notify.error({
          title: '',
          message: ''
        });
      }
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Divider__);
__vue__.use(__Row__)
     
    
  </script>

<style scoped>

</style>

  