
  <template>
  <div>
    <el-divider content-position="left">FormItem</el-divider>
    <el-row :gutter="10">
      <el-col :span="6">
        <el-input-number :min="1" :max="10" label=""></el-input-number>
      </el-col>
      <el-col :span="6">
        <el-input></el-input>
      </el-col>
      <el-col :span="6">
        <Cascader></Cascader>
      </el-col>
      <el-col :span="6">
        <el-color-picker></el-color-picker>
      </el-col>
      <el-col :span="6">
        <el-date-picker
          align="right"
          type="date"
          placeholder=""
          :picker-options="pickerOptions">
        </el-date-picker>
      </el-col>

      <el-col :span="6">
        <el-radio v-model="radio" label="1"></el-radio>
        <el-radio v-model="radio" label="2"></el-radio>
      </el-col>

      <el-col :span="6">
        <el-radio-group v-model="radio">
          <el-radio-button label="1"></el-radio-button>
          <el-radio-button label="2"></el-radio-button>
        </el-radio-group>
      </el-col>

      <el-col :span="6">
        <el-rate
          :value="3"
          show-text>
        </el-rate>
      </el-col>

      <el-col :span="6">
        <el-select placeholder="" value="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :disabled="item.disabled">
          </el-option>
        </el-select>
      </el-col>

      <el-col :span="6">
        <el-slider
          :value="5"
          :step="10"
          show-stops>
        </el-slider>
      </el-col>

      <el-col :span="6">
        <el-switch
          v-model="value1"
          active-text=""
          inactive-text="">
        </el-switch>
        <el-switch
          v-model="value2"
          disabled>
        </el-switch>
      </el-col>

      <el-col :span="6">
        <el-time-select
          v-model="value"
          :picker-options="{
            start: '08:30',
            step: '00:15',
            end: '18:30'
          }"
          placeholder="">
        </el-time-select>
      </el-col>

      <el-col :span="6">
        <el-time-picker
          :picker-options="{

        }"
          placeholder="">
        </el-time-picker>
      </el-col>

      <el-col :span="6">
        <el-checkbox v-model="checked"></el-checkbox>
      </el-col>

      <el-col :span="6">
        <el-checkbox-group v-model="radio">
          <el-checkbox-button label="1"></el-checkbox-button>
          <el-checkbox-button label="2"></el-checkbox-button>
        </el-checkbox-group>
      </el-col>

    </el-row>
  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Checkbox as __Checkbox__,CheckboxButton as __CheckboxButton__,CheckboxGroup as __CheckboxGroup__,Col as __Col__,ColorPicker as __ColorPicker__,DatePicker as __DatePicker__,Divider as __Divider__,Input as __Input__,InputNumber as __InputNumber__,Option as __Option__,Radio as __Radio__,RadioButton as __RadioButton__,RadioGroup as __RadioGroup__,Rate as __Rate__,Row as __Row__,Select as __Select__,Slider as __Slider__,Switch as __Switch__,TimePicker as __TimePicker__,TimeSelect as __TimeSelect__} from 'element-ui';
     
     
     
     
     import Cascader from './Cascader'

  export default {
    components: {
      Cascader
    },
    data() {
      return {
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: '',
          disabled: true
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        value: '',
        value1: '',
        value2: '',
        radio: '1',
        checked: true
      };
    }
  }
     __vue__.use(__Checkbox__);
__vue__.use(__CheckboxButton__);
__vue__.use(__CheckboxGroup__);
__vue__.use(__Col__);
__vue__.use(__ColorPicker__);
__vue__.use(__DatePicker__);
__vue__.use(__Divider__);
__vue__.use(__Input__);
__vue__.use(__InputNumber__);
__vue__.use(__Option__);
__vue__.use(__Radio__);
__vue__.use(__RadioButton__);
__vue__.use(__RadioGroup__);
__vue__.use(__Rate__);
__vue__.use(__Row__);
__vue__.use(__Select__);
__vue__.use(__Slider__);
__vue__.use(__Switch__);
__vue__.use(__TimePicker__);
__vue__.use(__TimeSelect__)
     
    
  </script>

<style lang="scss" scoped>
  .el-col {
    margin-bottom: 10px;
    min-height: 40px;
  }
</style>

  